<template>
  <f7-page>
    <f7-navbar back-link="Back" title="Virtual List"></f7-navbar>


    <!-- Search through this list -->
    <f7-list
      id="search-list"
      media-list
      virtual-list
      :virtual-list-params="{ items: items, height: 63, searchAll: searchAll, renderExternal: renderExternal }"
    >
      <ul>
        <!-- we will get the items we need to render from VL render external callback -->
        <f7-list-item
          v-for="(item, index) in vlData.items"
          :key="index"
          media-item
          link="#"
          :style="`top: ${vlData.topPosition}px`"
        >
          <div class="card demo-facebook-card">
            <div class="card-header">
              <div class="demo-facebook-name">{{item.title}}</div>
              <div class="demo-facebook-date">Monday at 2:15 PM</div>
            </div>
            <div class="card-content card-content-padding">
              <p>What a nice photo i took yesterday!</p><img src="http://lorempixel.com/1000/700/nature/8/" width="100%"/>
            </div>
            <div class="card-footer">
            </div>
          </div>

        </f7-list-item>
      </ul>
    </f7-list>

  </f7-page>
</template>
<script>
  export default {
    data: function () {
      //fake data for testing
      var items = [];
      for (var i = 1; i <= 10; i++) {
        items.push({
          title: 'Item ' + i,
          subtitle: 'Subtitle ' + i
        });
      }
      return {
        items: items,
        vlData: {},
      };
    },

    methods: {
      // Function to proceed search results
      searchAll: function (query, items) {
        var found = [];
        for (var i = 0; i < items.length; i += 1) {
          if (items[i].title.toLowerCase().indexOf(query) >= 0 || query.trim() === '') found.push(i);
        }
        return found; // return array with mathced indexes
      },
      renderExternal(vl, vlData) {
        this.vlData = vlData;
      },
    },
  }
</script>

<style scoped>
  .demo-facebook-card .card-header {
    display: block;
    /*padding: 10px;*/
  }
  .demo-facebook-card .demo-facebook-avatar {
    float: left;
  }
  .demo-facebook-card .demo-facebook-name {
    /*margin-left: 44px;*/
    font-size: 14px;
    font-weight: 500;
  }
  .demo-facebook-card .demo-facebook-date {
    /*margin-left: 44px;*/
    font-size: 13px;
    color: #8e8e93;
  }
  .demo-facebook-card .card-footer {
    background: #fafafa;
  }
  .demo-facebook-card .card-footer a {
    color: #81848b;
    font-weight: 500;
  }
  .demo-facebook-card .card-content img {
    display: block;
  }
  .demo-facebook-card .card-content-padding {
    /*padding: 15px 10px;*/
  }
  .demo-facebook-card .card-content-padding .likes {
    color: #8e8e93;
  }
</style>




